body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --mainBlue: #0000ff;
  --lightBlue: #304960;
  --mainWhite: #b2c2cf;
  --mainDark: #232528;
  --mainYellow: #ffa400;
}

body {
  font-family: 'Raleway', sans-serif !important;
  background: url(/film_shop/static/media/header.a4e1e681.png) no-repeat center center fixed !important;
  background-size: cover;
  color: #232528 !important;
  color: var(--mainDark) !important;
}

.text-title {
    font-family: 'Raleway', sans-serif;
    letter-spacing: 0.3rem;
    opacity: 0.8; 
    background: #ffffff;
}

.text-blue {
  color: #0000ff;
  color: var(--mainBlue);
  opacity: 0.8; 
  background: #ffffff;
}

.text-bright {
  color: #304960;
  color: var(--lightBlue);
  opacity: 0.8; 
  background: #ffffff;
}

.btn-black {
  background: transparent;
  font-size: 0.8rem !important;
  color: #232528;
  color: var(--mainDark);
  border-radius: 0 !important;
  border: 0.1rem solid #232528 !important;
  border: 0.1rem solid var(--mainDark) !important;
}

.btn-black:hover {
  background: #232528 !important;
  background: var(--mainDark) !important;
  color: #b2c2cf !important;
  color: var(--mainWhite) !important;
}

.cart-icon {
  cursor: pointer;
  font-size: 1.2rem;
  color: #ffa400;
  color: var(--mainYellow);
}
